import { mapState } from 'vuex'
import { getComments } from '../../../api/articles'
export default {
  name: 'article-comments',
  props: {
      article: {
          type: Object
      }
  },
   computed: {
      ...mapState(['user'])
   },
   data () {
       return {
           comments: []
       }
   },
   async mounted() {
      let { data: { comments } } = await getComments(this.article.slug)
      this.comments  = comments
   }
}
